import { Component, OnInit } from '@angular/core';
import { HttpClient,HttpHeaders } from '@angular/common/http';

@Component({
  selector: 'app-homepage',
  templateUrl: './homepage.component.html',
  styleUrls: ['./homepage.component.css']
})
export class HomepageComponent implements OnInit {
  public books =[]
  constructor(private http : HttpClient) { }

  ngOnInit() {
    this.http.get("https://abhishek-bookstore.herokuapp.com/api/home").subscribe((data:any) => {
      this.books = data;
     
    })
  }

  buy(book){
   this.http.post("https://abhishek-bookstore.herokuapp.com/api/wish",{
    "seller": book.seller,
    "name":book.name,
    "author" : book.author,
    "price" : book.price,
    "condition" : book.condition
  }).subscribe(res=> alert("You book has been added to cart"))
    
  }

  search(abc){
    this.http.post("https://abhishek-bookstore.herokuapp.com/api/search",{
      "name" : abc 
    }).subscribe((data:any) => {
      this.books = data;
     
    })
    
  }
  

  filter(a){
    console.log(a)
    if(a=="all")
    {
      this.http.get("https://abhishek-bookstore.herokuapp.com/api/filterc").subscribe((data:any) => {
        this.books = data;
       console.log("inside all")
      })
    }
    else{
    this.http.post("https://abhishek-bookstore.herokuapp.com/api/filterc",{
      "condition" : a
    }).subscribe((data:any) => {
      this.books = data;
     
    })
  }
  }

  filter2(b){
    console.log(b)
    if(b=="all")
    {
      this.http.get("https://abhishek-bookstore.herokuapp.com/api/filterp").subscribe((data:any) => {
        this.books = data;
       console.log("inside all")
      })
    }
    else{
    this.http.post("https://abhishek-bookstore.herokuapp.com/api/filterp",{
      "price" : b
    }).subscribe((data:any) => {
      this.books = data;
     
    })
  } 
  }

}
